export default {
	data() {
		return {
			zhuanye: [
				{ name: '计算机' },
				{ name: '驾驶员' },
				{ name: '汽车修理' },
				{ name: '煤工' },
				{ name: '平面设计' },
				{ name: '三维设计' },
				{ name: '网站建设' },
				{ name: '电子商务' }
			],
			zytypes: [
				{ name: '技术类' },
				{ name: '互联网' },
				{ name: '汽车修理' },
				{ name: '计算机' },
				{ name: '平面设计' },
				{ name: '三维设计' },
				{ name: '网站建设' },
				{ name: '电子商务' }
			],
			cardCode: '',
			zynum: 0,
			lbnum: 0,
			dianka: 0
		};
	},
	onLoad(options) {
		console.log(options);
		if (options.hascard === 'true') {
			this.dianka = 1;
		}
	},
	methods: {
		toExamine(e) {
			if (this.dianka < 1) {
				uni.showModal({
					title: '温馨提示',
					content: '您还没有购买该试卷点卡！',
					confirmText: '去购买',
					success(r) {
						if (r.confirm) {
							uni.navigateTo({
								url: 'examineCard/examineCard'
							});
						}
					}
				});
			} else {
				if (this.cardCode === '' || this.cardCode == null) {
					uni.showModal({
						title: '温馨提示',
						content: '请输入点卡码在进行考试！'
					});
				} else {
					uni.showModal({
						title: '温馨提示',
						content: '是否确认开始考试？',
						success(r) {
							if (r.confirm) {
								uni.navigateTo({
									url: '../examine/examine'
								});
							}
						}
					});
				}
			}
		},
		choose1(e) {
			console.log(this.zhuanye[e.currentTarget.dataset.index].name);
			this.zynum = e.currentTarget.dataset.index;
		},
		choose2(e) {
			console.log(this.zytypes[e.currentTarget.dataset.index].name);
			this.lbnum = e.currentTarget.dataset.index;
		}
	}
};
